.footer {
	color: white;
	font-size: 16px;
	font-weight: bold;
	display: flex;
	flex-direction: column;
	/* height: 7vh; */
	justify-content: space-around;
	z-index: 9999;
	position: relative;
  padding-bottom: 7vh;
}

.footer p {
	padding: 0 0 2vh 0;
}

.footer_links a {
	color: white;
	text-decoration: none;
	cursor: pointer;
	padding: 0.5vh 0 0.5vh;
  margin: 0 1.5vw;
}

.footer_links a:hover {
	border-top: 1px solid white;
	border-bottom: 1px solid white;
}


@media (max-width: 815px) {
	.footer p {
		padding: 0 0 0.5vh 0;
	}

	.footer_links {
		display: flex;
		flex-direction: column;
	}

	.footer_links a {
    width: 50vw;
    margin: .25vh auto;
    padding: 0.5vh 0 0.25vh;
  }

  .footer_links a:hover {
	border: none;
}
}
